import { NgModule } from "@angular/core";

import { Routes, RouterModule } from "@angular/router";

// Import Containers
import { DefaultLayoutComponent } from "./containers";

import { StationComponent } from "./views/station/station.component";

import { AddUserComponent } from "./views/add-user/add-user.component";

import { UserManageComponent } from "./views/user-manage/user-manage.component";

import { ReportComponent } from "./report/report.component";

import { LoginComponent } from "./auth/login/login.component";

import { StationAddComponent } from "./views/station-add/station-add.component";

import { ProfilesComponent } from "./views/profiles/profiles.component";

import { StationbyuserComponent } from "./views/stationbyuser/stationbyuser.component";

import { ForecastviewComponent } from "./forecastview/forecastview.component";

import { StationByoneComponent } from "./views/station-byone/station-byone.component";

import { ForecastebyuserComponent } from "./forecastebyuser/forecastebyuser.component";

import { ForecastestaionComponent } from "./forecastestaion/forecastestaion.component";

import { MapinglocationComponent } from "./mapinglocation/mapinglocation.component";

import { GraphComponent } from "./graph/graph.component";

import { TableexcelComponent } from "./tableexcel/tableexcel.component";
import { AtustationComponent } from "./atustation/atustation.component";
import { AuthokGraphComponent } from "./authok-graph/authok-graph.component";
import { AuthokReportComponent } from "./authok-report/authok-report.component";
import { AuthokExportdetailComponent } from "./authok-exportdetail/authok-exportdetail.component";
import { AuthokDetailComponent } from "./authok-detail/authok-detail.component";
export const routes: Routes = [
  {
    path: "",
    redirectTo: "login",
    pathMatch: "full",
  },

  {
    path: "login",
    component: LoginComponent,
    data: {
      title: "ລົງຊື່ເຂົ້າໃຊ້",
    },
  },

  {
    path: "tablexsml",
    component: TableexcelComponent,
    data: {
      title: "tablexsml",
    },
  },

  {
    path: "stationbycustomer",
    component: ForecastestaionComponent,
    data: {
      title: "stationbyone",
    },
  },

  {
    path: "stationmap",
    component: MapinglocationComponent,
    data: {
      title: "stationmap",
    },
  },

  {
    path: "forecastviewbyuser",
    component: ForecastebyuserComponent,
    data: {
      title: "ສະພາບອາກາດ",
    },
  },

  {
    path: "",
    component: DefaultLayoutComponent,
    data: {
      title: "ໜ້າຫຼັກ",
    },

    children: [
      {
        path: "dashboard",
        loadChildren: () =>
          import("./views/dashboard/dashboard.module").then(
            (m) => m.DashboardModule
          ),
      },

      {
        path: "usermanager",
        component: UserManageComponent,
        data: {
          title: "ຈັດການພະນັກງານ",
        },
      },

      {
        path: "stationmanage",
        component: StationComponent,
        data: {
          title: "ຈັດການຂໍ້ມູນສະຖານີ",
        },
      },

      {
        path: "adduser",
        component: AddUserComponent,
        data: {
          title: "ເພີ່ມຜູ້ໃຊ້ງານ",
        },
      },

      {
        path: "addstation",
        component: StationAddComponent,
        data: {
          title: "ເພີ່ມສະຖານີ",
        },
      },

      {
        path: "profiles",
        component: ProfilesComponent,
        data: {
          title: "Profile",
        },
      },
      //ເລົ້າເຕີ້ ອຸຕຸນິຍົມ

      {
        path: "forecastview",
        component: ForecastviewComponent,
        data: {
          title: "ສະພາບອາກາດ",
        },
      },
      {
        path: "graph",
        component: GraphComponent,
        data: {
          title: "ສະພາບອາກາດ",
        },
      },
      {
        path: "report",
        component: ReportComponent,
        data: {
          title: "ເບິ່ງລາຍງານ",
        },
      },
      {
        path: "stationbyone",
        component: StationByoneComponent,
        data: {
          title: "ກຼາບລົມ",
        },
      },

      // ຈົບ ເລົ້າເຕີ

      {
        path: "stationbyuser",
        component: StationbyuserComponent,
        data: {
          title: "ກຳນົດສະຖານີຜູ້ໃຊ້",
        },
      },
      {
        path: "climate-station",
        component: AtustationComponent,
        data: {
          title: "ສະຖານີ ອຸທົກກະສາດ",
        },
      },
      {
        path: "climate-graph",
        component: AuthokGraphComponent,
        data: {
          title: "ສະຖານີ ອຸທົກກະສາດ",
        },
      },
      {
        path: "climate-report",
        component: AuthokReportComponent,
        data: {
          title: "ສະຖານີ ອຸທົກກະສາດ",
        },
      },

      {
        path: "climate-reportdetail",
        component: AuthokExportdetailComponent,
        data: {
          title: "ສະຖານີ ອຸທົກກະສາດ",
        },
      },
      {
        path: "climate-detail",
        component: AuthokDetailComponent,
        data: {
          title: "ສະຖານີ ອຸທົກກະສາດ",
        },
      },
    ],
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      relativeLinkResolution: "legacy",
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
