


import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { APIService } from "./api.service";
@Injectable({
  providedIn: 'root'
})
export class StationService extends APIService {
  public url: string = this.getBaseUrl();
  public token = '';


  getStation(param: any): Observable<any> {
    const header = this.headerBase('stationListAll');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  getStationPage(param: any): Observable<any> {
    const header = this.headerBase('stationListPage');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  getStationAll(param: any): Observable<any> {
    const header = this.headerBase('stationListAll');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  getByuser(param: any): Observable<any> {
    const header = this.headerBase('stationListPageByUser');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }
  getStationAllByUser(param: any): Observable<any> {
    const header = this.headerBase('stationListAllByUser');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }
  getById(param: any): Observable<any> {
    const header = this.headerBase('getStation');

    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }
  getStationBypart(param: any): Observable<any> {
    const header = this.headerBase('stationListPageByPart');

    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  createStation(param: any): Observable<any> {
    const header = this.headerBase('addStation');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  updateStation(param: any): Observable<any> {
    const header = this.headerBase('updateStation');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  deletestation(param: any): Observable<any> {
    const header = this.headerBase('deleteStation');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }

  getStationSynopticAllByUser(param: any): Observable<any> {
    const header = this.headerBase('stationSynopticListAllByUser');
    return this.http.post(this.url + 'station.api.php', param, { headers: header });
  }


  countSynoptic(param: any): Observable<any> {
    const header = this.headerBase('countStationSynoptic');
    return this.http.post(this.url + 'report.api.php', param, { headers: header });
  }

  countHydrology(param: any): Observable<any> {
    const header = this.headerBase('countStationHydrology');
    return this.http.post(this.url + 'report.api.php', param, { headers: header });
  }
}


