import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { StationService } from "../../services/station.service";
import Swal from "sweetalert2";
import { AppServiceCsv } from "../../services/mycsv";

@Component({
  selector: "app-report",
  templateUrl: "./report.component.html",
  styleUrls: ["./report.component.scss"],
})
export class ReportComponent implements OnInit {
  public isCollapsed: boolean = false;
  public page: number = 1;
  public limit: number = 100;
  public keyword: string = "";
  public station_data: any;

  public Myselected: boolean;
  public stationListAllOne: Array<any>;
  public stationListAllTwo: Array<any>;

  //data
  public dateNow: any = moment().format("llll");
  public datalist: any;
  public station_id: string;
  public array_id: Array<number> = [];
  public userId: number;
  public startTime: string;
  public endTime: string;
  public checkAdmin: boolean = false;

  constructor(
    private router: Router,
    public stationservice: StationService,
    private appService: AppServiceCsv
  ) {}

  ngOnInit(): void {
    const local = JSON.parse(localStorage.getItem("userData"));
    this.userId = local.id;
    if (local.role === "admin") {
      this.checkAdmin = true;
    } else {
      this.checkAdmin = false;
    }
    this.getListstationAll();
  }

  changeGender(e) {
    let idchange = e.target.value;
    let myid = parseInt(idchange);
    if (e.target.checked) {
      this.array_id.push(myid);
    } else {
      for (let index = 0; index < this.array_id.length; index++) {
        const element = this.array_id[index];
        if (myid == element) {
          this.array_id.splice(index, 1);
        }
      }
    }
    console.log("ssssssssssssss", this.array_id);
  }
  getListstationAll() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      userid: this.userId,
    };
    this.stationservice.getStationSynopticAllByUser(data).subscribe((res) => {
      if (res.status == 1) {
        this.station_data = res.data;
        console.log(this.station_data);

        let result = res.data;

        let numAll: number = parseInt(result.length);
        let myNum: number = Math.ceil(numAll / 2);

        let dataOne = result.slice(0, myNum);
        let dataTwo = result.slice(myNum);

        this.stationListAllOne = [];
        this.stationListAllOne = dataOne;

        this.stationListAllTwo = [];
        this.stationListAllTwo = dataTwo;
      }
    });
  }

  clickLselectAll(e) {
    let checkall = e.target.checked;

    let checkboox = document.getElementsByTagName("input");
    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        if (checkboox[i].type == "checkbox") {
          checkboox[i].checked = true;
        }
      }

      let stationid = this.station_data;

      this.array_id = [];

      for (let index = 0; index < stationid.length; index++) {
        const element = stationid[index];

        this.array_id.push(parseInt(element.id));
      }

      console.log("myId ", this.array_id);
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        if (checkboox[index].type == "checkbox") {
          checkboox[index].checked = false;
        }
      }

      this.array_id = [];

      console.log("myId ", this.array_id);
    }

    // var checkboxes = document.querySelectorAll('input[type="checkbox"]');
    // for (var i = 0; i < checkboxes.length; i++) {
    //     if (checkboxes[i] != source)
    //         checkboxes[i].checked = source.checked;
  }

  checkfromReportExcel() {
    if (this.array_id.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "ຂໍອະໄພ!",
        text: "ກະລຸນາເລືອກສະຖານີ",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "ຂໍອະໄພ!",
        text: "ກະລຸນາເລືອກວັນທີເລີ່ມຕົ້ນ",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "ຂໍອະໄພ!",
        text: "ກະລຸນາເລືອກວັນທີສິ້ນສຸດ",
        showConfirmButton: true,
      });
    } else {
      this.exportToexcel();
    }
  }

  checkfromReportCSV() {
    if (this.array_id.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "ຂໍອະໄພ!",
        text: "ກະລຸນາເລືອກສະຖານີ",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "ຂໍອະໄພ!",
        text: "ກະລຸນາເລືອກວັນທີເລີ່ມຕົ້ນ",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "ຂໍອະໄພ!",
        text: "ກະລຸນາເລືອກວັນທີສິ້ນສຸດ",
        showConfirmButton: true,
      });
    } else {
      this.exportTocsv();
    }
  }

  exportToexcel() {
    this.router.navigate(["/tablexsml"], {
      queryParams: {
        stationId: this.array_id,
        startTime: this.startTime,
        endTime: this.endTime,
        status: "excel",
      },
    });
  }

  exportTocsv() {
    this.router.navigate(["/tablexsml"], {
      queryParams: {
        stationId: this.array_id,
        startTime: this.startTime,
        endTime: this.endTime,
        status: "csv",
      },
    });
  }
}

export class Station {
  altitude: string;
  id: number;
  laoName: string;
  latitude: string;
  longitude: string;
  name_eng: string;
  part: string;
  station_model: string;
  station_number: string;
  station_phonenumber: string;
  type: string;
}
