import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import * as XLSX from 'xlsx';
import * as FileSaver from 'file-saver';
import * as moment from 'moment';
const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';

@Injectable({
  providedIn: 'root'
})


export class XlsxService {
  fileName = 'ExcelSheet.xlsx';
  constructor() {

  }

  exportCSV(data: Array<any> = []) {

    // var p = [{
    //   "p1": "value1",
    //   "p2": "value2",
    //   "p3": "value3"
    // }, {
    //   "p1": "value1",
    //   "p2": "value2",
    //   "p3": "value3"
    // }, {
    //   "p1": "value1",
    //   "p2": "value2",
    //   "p3": "value3"
    // }
    // ];

    let Results = [];

    let myKey = [];
    for (var key in data[0]) {
      if (data[0].hasOwnProperty(key)) {
        myKey.push(key);
      }
    }

    Results.push(myKey);

    for (let index = 0; index < data.length; index++) {
      const element = data[index];
      let myValue = [];
      for (var key in element) {
        if (data[0].hasOwnProperty(key)) {
          myValue.push(element[key]);
        }
      }
      Results.push(myValue);

    }


    let CsvString = "";
    Results.forEach(function (RowItem, RowIndex) {
      RowItem.forEach(function (ColItem, ColIndex) {
        CsvString += ColItem + ',';
      });
      CsvString += "\r\n";
    });
    CsvString = "data:text/csv," + encodeURIComponent(CsvString);
    var x = document.createElement("A");
    let myDate = moment().format('YYYY-MM-DD H:m:s')
    x.setAttribute("href", CsvString);
    x.setAttribute("download", "ExportFileCSV-" + myDate + ".csv");
    document.body.appendChild(x);
    x.click();

  }



  exportToExcelFile(data: Array<any>) {
    let myDate = moment().format('YYYY-MM-DD H:m:s')
    console.log(myDate);

    const workBook = XLSX.utils.book_new(); // create a new blank book
    const workSheet = XLSX.utils.json_to_sheet(data);

    XLSX.utils.book_append_sheet(workBook, workSheet, 'data'); // add the worksheet to the book
    XLSX.writeFile(workBook, 'ExportFileExcel-' + myDate + '.xlsx'); // initiate a file download in browser
  }


  downloadFile(data, filename = "data") {

    let myKey = [];
    for (var key in data[0]) {
      if (data[0].hasOwnProperty(key)) {
        myKey.push(key);
      }
    }

    let csvData = this.ConvertToCSV(data, myKey);
    console.log(csvData);
    let blob = new Blob(["\ufeff" + csvData], {
      type: "text/csv;charset=utf-8;",
    });
    let dwldLink = document.createElement("a");
    let url = URL.createObjectURL(blob);
    let isSafariBrowser =
      navigator.userAgent.indexOf("Safari") != -1 &&
      navigator.userAgent.indexOf("Chrome") == -1;
    if (isSafariBrowser) {
      //if Safari open in new window to save file with random filename.
      dwldLink.setAttribute("target", "_blank");
    }
    dwldLink.setAttribute("href", url);
    dwldLink.setAttribute("download", filename + ".csv");
    dwldLink.style.visibility = "hidden";
    document.body.appendChild(dwldLink);
    dwldLink.click();
    document.body.removeChild(dwldLink);
  }
  ConvertToCSV(objArray, headerList) {
    let array = typeof objArray != "object" ? JSON.parse(objArray) : objArray;
    let str = "";
    let row = "No,";
    for (let index in headerList) {
      row += headerList[index] + ",";
    }
    row = row.slice(0, -1);
    str += row + "\r\n";
    for (let i = 0; i < array.length; i++) {
      let line = i + 1 + "";
      for (let index in headerList) {
        let head = headerList[index];
        line += "," + array[i][head];
      }
      str += line + "\r\n";
    }
    return str;
  }
}
