import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import * as moment from "moment";
import { StationService } from "../../../services/station.service";
import { SynoticService } from "../../../services/synotic.service";
import {
  WindRose,
  WindDescription,
  Service,
} from "../../../services/chartservices";

@Component({
  selector: "app-station-byone",
  templateUrl: "./station-byone.component.html",
  styleUrls: ["./station-byone.component.scss"],
  providers: [Service],
})
export class StationByoneComponent implements OnInit {
  public isCollapsed: boolean = false;
  windRoseData: WindRose[];
  windSources: WindDescription[];

  public limit: number = 6;
  public page: number = 1;
  public keyword: string = "";

  //data
  public stationList_data: any;
  public datalist: any;
  public NameStation: string;
  public checkAdmin: boolean = false;

  constructor(
    private router: Router,
    // private serviceGetstation: HydrologyService,
    private synoticservice: SynoticService,
    private stationList: StationService,
    public service: Service,
    private route: ActivatedRoute
  ) {
    this.windRoseData = service.getWindRoseData();
    this.windSources = service.getWindSources();
  }
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("LLLL");

  ngOnInit(): void {
    const local = JSON.parse(localStorage.getItem("userData"));
    if (local.role == "admin") {
      this.checkAdmin == true;
    } else {
      this.checkAdmin == false;
    }
    // this.loadingPage()
    // this.getStationList()
    this.loadingrouter();
  }
  loadingPage() {
    // this.getSynopticListBystation()
    // this.getSynopticListPage('1')
  }
  loadingrouter() {
    this.route.queryParams.subscribe((params) => {
      this.getsynoticBystation(params.stationId);
      console.log("check id refesh", this.getsynoticBystation);
    });
  }

  sentdataToservice(data: any) {
    console.log("data sent to service chart", data);

    // this.service.getWindSources(data)
    // this.service.getWindRoseData(data);
    this.windRoseData = this.service.getWindRoseData(data);
    this.windSources = this.service.getWindSources(data);
  }
  onLegendClick(e: any) {
    var series = e.target;
    if (series.isVisible()) {
      series.hide();
    } else {
      series.show();
    }
  }

  getsynoticBystation(stationId: number) {
    const data = {
      limit: (this.limit = 3),
      page: this.page,
      station_id: stationId,
    };
    console.log("get station sonitic by station", data);
    try {
      this.synoticservice.getSynoticBystation(data).subscribe((res) => {
        if (res.data) this.datalist = res.data;
        console.log("getSynoticBystation", this.datalist.Data);
        const mydata = res.data?.Data as ModelstationList;
        this.setUp_down(this.datalist.Data);
        // if (mydata.Data[0].air_pressure) {
        // }

        console.log("set data to serice chart", mydata);
        this.sentdataToservice(mydata);
      });
    } catch (error) {
      console.log(error);
    }
  }
  setUp_down(data: Array<any>) {
    for (let index = 0; index < data.length; index++) {
      const element = data[index];
      if (index < data.length) {
      }
    }
  }
  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);

    // this.getSynopticListPage()
  }
}

export class ModelstationList {
  Data: Array<list>;
}

export class list {
  alt: number;
  id: number;
  lat: number;
  lng: number;
  name_eng: string;
  name_lao: string;
  part: string;
  station_model: string;
  station_number: string;
  station_phonenumber: number;
  type: string;
  air_pressure: number;
  air_temperature: number;
  avg_wind_direction: number;
  avg_wind_speed: number;
  dewpoint: number;
  ext_batt: number;
  max_air_temperature: number;
  metpak_status: number;
  min_air_temperature: number;
  modern_signal: number;
  moisture: number;
  net_radiation: number;
  rain_fall: number;
  rain_tips: number;
  relative_humidity: number;
  sid: number;
  station_id: number;
  time: string;
  wind_gust: number;
  wind_status: number;
}
