import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { AuthService } from "../../../services/auth.service";
const { version: appVersion } = require('../../../../package.json');
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public Phonenumber: string;
  public password: string;
  public platform: string = "forecast";
  appVersion:string='0.0.0';
  constructor(
    private route: Router,
    private authservice: AuthService

  ) { }

  ngOnInit(): void {
    this.Phonenumber = 'admin';
    this.password = '123456';
    this.appVersion=appVersion;
  }


  login() {


    const data = {
      username: this.Phonenumber,
      password: this.password,
    }
    console.log('ss', data);
    this.authservice.login(data).subscribe
      (res => {
        console.log('sssssssssssssssssssssss', res);

        console.log('login data', res.data);

        if (res.data.role == 'admin') {
          localStorage.setItem(('Token'), (res.Token))
          localStorage.setItem(('userData'), JSON.stringify(res.data))
          this.alertsuccess()
          this.route.navigate(['/dashboard'])
        } else if (res.data.role == 'user') {
          this.route.navigate(['/forecastview'])
          localStorage.setItem(('Token'), (res.Token))
          localStorage.setItem(('userData'), JSON.stringify(res.data))
          this.alertsuccess()
        } else {
          this.alertfalse()

        }

        // 

      })
  }

  alertsuccess() {
    Swal.fire(
      'ເຂົ້າສູ່ລະບົບສຳເລັດ',
      'ຄລິກຕົກລົງເພື່ອປິດໜ້ານີ້!',
      'success'
    )
  }

  alertfalse() {
    Swal.fire({
      icon: 'error',
      title: 'Oops...',
      text: 'Something went wrong!',
      // footer: '<a href="">Why do I have this issue?</a>'
    })
  }
  keyusername(event: KeyboardEvent) {
    this.Phonenumber = (event.target as HTMLInputElement).value
  }

  keypassword(event: KeyboardEvent) {
    this.password = (event.target as HTMLInputElement).value
  }
}
