import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { StationService } from '../../../services/station.service';
import Swal from 'sweetalert2';
import { PageingtationService } from '../../../services/pageingtation.service';

@Component({
  selector: 'app-station',
  templateUrl: './station.component.html',
  styleUrls: ['./station.component.scss']
})
export class StationComponent implements OnInit {
  setDataUpdate: any = {};
  keyusername: any = {};
  selectedOption = '';
  public page: number = 1
  public limit: number = 10
  public keyword: string = ''

  public station_data: Array<Station>

  public stationCountAll: number;
  public stationCountNorth: number;
  public stationCountCenter: number;
  public stationCountSouth: number;



  public station: Array<any>
  constructor(
    public router: Router,
    public stationservice: StationService,
    public pageingtation: PageingtationService

  ) { }

  ngOnInit(): void {
    this.loading()

  }

  loading() {
    this.getListall()
    this.getStationByPartNorth();
    this.getStationByPartCenter();
    this.getStationByPartSouth();

  }
  getListall() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword
    }
    this.stationservice.getStationPage(data).subscribe
      (res => {
        console.log(res);
        if (res.status == 1) {
          this.station_data = res

          console.log('sssssssssssssssssss', this.station_data);

          this.stationCountAll = res.data.Datatotal;

        }
      })
  }

  onsearching(ev: any) {
    this.keyword = (event.target as HTMLInputElement).value
    this.getListall()

  }

  click_pagemodalTo(page: any) {
    this.pageingtation.pageingtations
    if (page) {
      this.page = page;
      this.loading();

    }
  }
  deletestation(id: number) {
    Swal.fire({
      title: 'ທ່ານຕ້ອງການລົບສະຖານີ້ບໍ່?',
      text: "ເມື່ອງລົບແລ້ວບໍ່ສາມກູ້ຄືນໄດ້!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'ຕົກລົງ',
      cancelButtonText: 'ຍົກເລີກ'
    }).then((result) => {
      if (result.isConfirmed) {
        const data = {
          id: id
        }
        this.stationservice.deletestation(data).subscribe
          (res => {
            console.log(res);
            if (res.status == 1) {
              Swal.fire({
                icon: 'success',
                title: 'ລົບສະຖານີສຳເລັດ',
                showConfirmButton: false,
                timer: 1500
              })
              this.getListall()

            } else {
              Swal.fire({
                icon: 'error',
                title: 'ຂໍອະໄພ!',
                text: res.message,
                showConfirmButton: true,
              })
              this.getListall()

            }

          })

      }
    })

  }
  updateEmployee() { }
  addStation(id: number) {
    this.router.navigate(['/addstation'], { queryParams: { statuscheck: 'addstation' } })
  }
  updatestation(id: number) {
    this.router.navigate(['/addstation'], { queryParams: { statuscheck: 'updatestation', stationId: id } })

  }

  getStationByPartNorth() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      part: 'north'
    }
    this.stationservice.getStationBypart(data).subscribe
      (res => {
        console.log(res);
        if (res.status == 1) {

          this.stationCountNorth = res.data.Datatotal;


        }
      })
  }

  getStationByPartCenter() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      part: 'center'
    }
    this.stationservice.getStationBypart(data).subscribe
      (res => {
        console.log(res);
        if (res.status == 1) {

          this.stationCountCenter = res.data.Datatotal;


        }
      })
  }

  getStationByPartSouth() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      part: 'south'
    }
    this.stationservice.getStationBypart(data).subscribe
      (res => {
        console.log(res);
        if (res.status == 1) {

          this.stationCountSouth = res.data.Datatotal;

        }
      })
  }
}


export class Station {
  altitude: string
  id: number
  laoName: string
  latitude: string
  longitude: string
  name: string
  station: string
  station_model: string
  station_phonenumber: string
  part: string
}

