import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { HydrologyService } from "../../services/hydrology.service";
import { StationService } from "../../services/station.service";

// import {
//   WindRose,
//   WindDescription,
//   Service,
// } from "../../services/chartservices";
// import { SynoticService } from "../../services/synotic.service";
import { NgxSpinnerService } from "ngx-spinner";
@Component({
  selector: "app-atustation",
  templateUrl: "./atustation.component.html",
  styleUrls: ["./atustation.component.scss"],
  // providers: [Service],
})
export class AtustationComponent implements OnInit {
  public isCollapsed: boolean = false;

  public limit: number = 12;
  public page: number = 1;
  public keyword: string = "";

  //data

  public hydrology_data: any;
  constructor(
    private router: Router,
    private hydrologyService: HydrologyService,
    private spinner: NgxSpinnerService
  ) {}
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("llll");

  ngOnInit(): void {
    this.loadingPage();
  }
  loadingPage() {
    this.getStationListByser();
  }
  getStationListByser() {
    const data = {
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    console.log("getHydrologyList", data);
    this.spinner.show();
    this.hydrologyService.getHydrologyList(data).subscribe((res) => {
      this.hydrology_data = res.data;
      console.log("getHydrologyList", this.hydrology_data);
      this.spinner.hide();
    });
  }

  getStationListBypart(part: string) {
    const data = {
      part: part,
      limit: (this.limit = 10),
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get by getStationListBypart", data);

    this.hydrologyService.getHydrologyListPart(data).subscribe((res) => {
      this.hydrology_data = res.data;

      console.log("getStationList by part okokokkkok", res);
    });
  }

  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);

    this.getStationListByser();
  }

  // onsearching(keyword) {
  //   this.keyword = keyword

  //   this.getStationList()

  // }
  stationlist(station_id: number) {
    console.log("tetsttsttsts");

    this.router.navigate(["/climate-detail"], {
      queryParams: { station_id: station_id },
    });
  }
}
